
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import scroll from '@/components/scroll/scroll'
import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Tab,TabItem,Swiper, SwiperItem,InlineLoading,scroll,slide
},
mounted(){
this.init();
},
data() {
return {
noData: false,
tabIndex: 0,
tabWidth: '',
pageIndex1:1,
pageIndex2:1,
pageIndex3:1,
pageIndex4:1,
pageSize:15,
IJZZT: 0, // 0或者null或者空字符串：全部   1、待就诊 2、已就诊 3、 已取消
list1: [],
list2: [],
list3: [],
list4: [],
curIndex: 0,
}

},
methods: {
init(){  // 初始化列表
let list = 'list' + (this.tabIndex + 1);
let pageIndex = 'pageIndex' + (this.tabIndex + 1);
this.$ajax.post('',{
"MSH.1": "myappointment",
"MSH.2": "listAppointment",
'token':this.$root.loginInfo.token, //token
'IJZZT':this.IJZZT, //    预约状态
'pageIndex':this[pageIndex],
'pageSize':15,
}).then(res => {
this.noData = false;
if(res.rows && res.rows.length > 0){
this[list] = this[list].concat(res.rows);
}else{
if(this[pageIndex] > 1){
this.$vux.toast.text("没有更多了", "bottom");
}else{
this.noData = true;
}


}


}).catch(error => {
console.log(error);
})
},
getJZZT(str){  // 获取就诊状态
switch (str) {
case 0:
return '全部';
break;
case 1:
return '待就诊';
break;
case 2:
return '已就诊';
break;
case 3:
return '已取消';
break;

default:
return str
break;
}
},
pullingUp(){
let pageIndex = 'pageIndex' + (this.tabIndex + 1);
this[pageIndex]++;
this.init();
},
slide(index){
this.$refs.slide.goToPage(index);
}
},
watch: {
tabIndex(val){
this.noData = false;
this.IJZZT = val;
let list = 'list' + (val + 1);
if(this[list].length > 0){
return;
}
this.init();
}
}

}



